import React, { Component } from "react";
import RouteOne from "./components/MockRoutes/RouteOne";
import RouteTwo from "./components/MockRoutes/RouteTwo";
import RouteThree from "./components/MockRoutes/RouteThree";
import { Route, Switch, Router, Link } from "react-router-dom";
import NavigationHelper from "./components/NavigationHelper/NavigationHelper";
import { createBrowserHistory } from "history";

const Routes = () => (
  <Router history={createBrowserHistory()}>
    <Link style={{ margin: "0 15px" }} to="/android-testing/one">
      one
    </Link>
    <Link style={{ margin: "0 15px" }} to="/android-testing/two">
      two
    </Link>
    <Link style={{ margin: "0 15px" }} to="/android-testing/three">
      three
    </Link>
    <Link style={{ margin: "0 15px" }} to="/android-testing">
      home
    </Link>

    <NavigationHelper />
    <Switch>
      <Route exact path="/android-testing/one" component={RouteOne} />
      <Route exact path="/android-testing/two" component={RouteTwo} />
      <Route exact path="/android-testing/three" component={RouteThree} />
      <Route exact path="/android-testing" />
    </Switch>
  </Router>
);

class App extends Component {
  render() {
    return (
      <div className="App">
        <div style={{ height: "100px", width: "100px", background: "red" }} />
        <Routes />
      </div>
    );
  }
}

export default App;
