import React, { Component } from "react";
import { connect } from "react-redux";
import { setA11yContent } from "../NavigationHelper/actions";
import Alert from "./Alert";

class RouteOne extends Component {
  state = {
    show: false
  };

  componentDidMount() {
    this.props.setA11yContent("route one");
    setTimeout(() => {
      this.setState({ show: true });
    }, 200);
  }

  renderAlert = () => {
    if (this.state.show) {
      return <Alert>this is the alert content bitch</Alert>;
    }
    return null;
  };

  render() {
    return (
      <div>
        <div>{this.renderAlert()}</div>
        {/* <Alert>this is the alert</Alert> */}
        route one
      </div>
    );
  }
}

export default connect(
  null,
  { setA11yContent }
)(RouteOne);
